.vmdr-pic {
	margin-top: 2em;
}
.text {
	max-width: 500px;
	position: relative;
	z-index: 2;
}
@media (min-width: 684px) {
	.vmdr-pic {
		margin-top: 1em;
		margin-right: -3em;
		float: right;
		width: 400px;
	}
}
@media (min-width: 720px) {
	.vmdr-pic {
		width: 400px;
	}
}
@media (min-width: 850px) {
	.vmdr-pic {
		width: 500px;
	}
}
@media (min-width: 1300px) {
	.vmdr-pic {
		width: 580px;
		width: auto;

		margin-right: -8em;
	}
}